import {Component, Input, OnInit} from '@angular/core';
import {Furniture} from '../../../models/Furniture';
import {FurnitureServiceService} from '../../../services/furniture-service.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';

@Component({
  selector: 'app-furniture-item',
  templateUrl: './furniture-item.component.html',
  styleUrls: ['./furniture-item.component.css']
})
export class FurnitureItemComponent implements OnInit {

  @Input() item: Furniture;

  constructor(private route: ActivatedRoute,  private location: Location,
              private router: Router, private furnitureService: FurnitureServiceService) { }

  ngOnInit() {
    this.getItem();
  }


  getItem() {
    const id = +this.route.snapshot.paramMap.get('id');
    this.furnitureService.getOneFurniture(id).subscribe(item => this.item = item);
  }

  goBack() {
    this.location.back();
  }


}
