import {Component, Input, OnInit} from '@angular/core';
import {FurnitureServiceService} from '../../services/furniture-service.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Furniture} from '../../models/Furniture';
import {Location} from '@angular/common';

@Component({
  selector: 'app-furniture-edit',
  templateUrl: './furniture-edit.component.html',
  styleUrls: ['./furniture-edit.component.css']
})
export class FurnitureEditComponent implements OnInit {

  @Input() furniture: Furniture;

  constructor(private route: ActivatedRoute, private location: Location, private furnitureService: FurnitureServiceService,
              private router: Router) { }

  ngOnInit() {
    this.getFurnitureItem();
  }


  getFurnitureItem(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.furnitureService.getOneFurniture(id).subscribe(furniture => this.furniture = furniture);
  }

  goBack() {
    this.location.back();
  }

  save() {
    this.furnitureService.updateFurniture(this.furniture).subscribe(navigate => this.router.navigate(['furniture']));
  }

}
