import { Component, OnInit } from '@angular/core';
import {Observable, Subject} from 'rxjs';
import {Furniture} from '../../../models/Furniture';
import {FurnitureServiceService} from '../../../services/furniture-service.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-furniture-list',
  templateUrl: './furniture-list.component.html',
  styleUrls: ['./furniture-list.component.css']
})
export class FurnitureListComponent implements OnInit {

  items$: Observable <Array<Furniture>>;

  constructor(private furnitureService: FurnitureServiceService, private router: Router) { }

  ngOnInit() {
    this.updateList();
  }
  updateList() {
    this.items$ = this.furnitureService.getFurniture();
  }

  onEditFurniture(id: number): void {
    this.router.navigate(['/furniture-edit/', id]);

  }

  addFurniture() {
    this.router.navigate(['/furniture-add']);
  }

  deleteFurniture(id: number) {
    console.log(`deleting furniture item ${id}`);
    this.furnitureService.deleteFurniture(id).subscribe(navigate => this.updateList());
    // this.router.navigate(['/furniture']);
  }

}
