import { Component, OnInit } from '@angular/core';
import {Furniture} from '../../models/Furniture';
import {FurnitureServiceService} from '../../services/furniture-service.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';

@Component({
  selector: 'app-furniture-add',
  templateUrl: './furniture-add.component.html',
  styleUrls: ['./furniture-add.component.css']
})
export class FurnitureAddComponent implements OnInit {

  furniture: Furniture;

  constructor(private furnitureService: FurnitureServiceService, private route: ActivatedRoute, private location: Location,
              private router: Router) {this.furniture = new Furniture(); }

  ngOnInit() {
  }

  addFurniture( name: string, code: string, price: number, comment: string, type: string) {
    this.furniture.name = name;
    this.furniture.code = code;
    this.furniture.price = price;
    this.furniture.comment = comment;
    this.furniture.type = type;

    this.furnitureService.addFurniture(this.furniture).subscribe( navigate => this.router.navigate(['furniture']));
  }

  goBack() {
    this.location.back();
  }

}
