import {Component, Input, OnInit} from '@angular/core';
import {User} from '../../models/User';
import {UserService} from '../../services/user.service';
import {Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-user-edit',
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.css']
})
export class UserEditComponent implements OnInit {

  @Input() user: User;

  constructor(private userService: UserService, private route: ActivatedRoute, private location: Location, private router: Router) { }

  ngOnInit() {
    this.getUser();
  }

  getUser(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.userService.getOneUser(id).subscribe(user => this.user = user);
  }

  goBack() {
    this.location.back();
  }

  save() {
    this.userService.updateUser(this.user).subscribe(navigate => this.router.navigate(['users']));
  }

}
