import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {FurnitureListComponent} from '../components/furniture-list/furniture-list/furniture-list.component';
import {PageNotFoundComponent} from '../page-not-found/page-not-found.component';
import {UserListComponent} from '../components/user-list/user-list.component';
import {AppComponent} from '../app.component';
import {FurnitureItemComponent} from '../components/furniture-item/furniture-item/furniture-item.component';
import {UserItemComponent} from '../components/user-item/user-item.component';
import {FurnitureEditComponent} from '../components/furniture-edit/furniture-edit.component';
import {UserEditComponent} from '../components/user-edit/user-edit.component';
import {FurnitureAddComponent} from '../components/furniture-add/furniture-add.component';
import {UserAddComponent} from '../components/user-add/user-add.component';

const routes: Routes = [
  {path: '', redirectTo: '/users', pathMatch: 'full'},
  {path: 'users', component: UserListComponent},
  {path: 'user/:id', component: UserItemComponent},
  {path: 'user-edit/:id', component: UserEditComponent},
  {path: 'user-add', component: UserAddComponent},
  {path: 'furniture-item/:id', component: FurnitureItemComponent},
  {path: 'furniture-edit/:id', component: FurnitureEditComponent},
  {path: 'furniture-add', component: FurnitureAddComponent},
  {path: 'furniture', component: FurnitureListComponent},
  { path: '**', component: PageNotFoundComponent },
  ];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {onSameUrlNavigation: `reload`}),
    CommonModule
  ],
  exports: [RouterModule],
  declarations: [],
})
export class AppRoutingModule { }
