import { Injectable } from '@angular/core';



import {HttpClient} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {Message} from '@angular/compiler/src/i18n/i18n_ast';
import {catchError, tap} from 'rxjs/operators';
import {Furniture} from '../models/Furniture';


@Injectable({
  providedIn: 'root'
})
export class FurnitureServiceService {

  constructor(private httpClient: HttpClient) { }

  //NAPRAVITI FURNITURE CONTROLLER
  getOneFurniture(id: number): Observable<Furniture> {
    return this.httpClient
      .get<Furniture>( '/api/furniture/' + id )
      .pipe(
        tap(a => this.log(`Loaded subject with id "${a.id}"`)),
        catchError(this.handleError<Furniture>('getFurniture')));
  }

  getFurniture(): Observable<Furniture[]> {
    return this.httpClient.get<Furniture[]>('/api/furniture').pipe(
      tap(_ => this.log(`Furniture loaded`)), catchError(this.handleError<Furniture[]>('getFurniture')));
  }

  addFurniture(furniture: Furniture): Observable<Furniture> {
    return this.httpClient
      .post<Furniture>('/api/furniture', furniture/*, httpOptions*/)
      .pipe(
        tap(a => this.log(`Added furniture item with id "${a.id}"`)),
        catchError(this.handleError<Furniture>('addFurniture')));
  }


  updateFurniture(furniture: Furniture): Observable<Furniture> {
    return this.httpClient
      .put<Furniture>('/api/furniture/' + furniture.id, furniture)
      .pipe(
        tap(a => this.log(`Updated furniture with id "${a.id}"`)),
        catchError(this.handleError<Furniture>('updateFurniture')));
  }

  deleteFurniture(id: number): Observable<any> {
    return this.httpClient.delete<Message>('/api/furniture/' + id).pipe(
      tap(_ => this.log(`Furniture item Deleted!`)), catchError(this.handleError<Message>('deleteFurniture')));
  }



  private log(message: String) {
    // this.messageService.add('SubjectService: ' + message);
    alert('FurnitureService: ' + message);
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log(error);
      this.log(`${operation} failed: ${error.message}`);
      return of (result as T);
    };
  }

}
