import { Injectable } from '@angular/core';
import {Furniture} from '../models/Furniture';
import {Observable, of} from 'rxjs';
import {User} from '../models/User';
import {Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {catchError, tap} from 'rxjs/operators';
import {Message} from '@angular/compiler/src/i18n/i18n_ast';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private httpClient: HttpClient ) { }

  getOneUser(id: number): Observable<User> {
    return this.httpClient
      .get<User>( '/api/users/' + id )
      .pipe(
        tap(a => this.log(`Loaded user with id "${a.id}"`)),
        catchError(this.handleError<User>('getOneUser')));
  }

  getUsers(): Observable<User[]> {
    return this.httpClient.get<User[]>('/api/users').pipe(
      tap(_ => this.log(`Users loaded`)), catchError(this.handleError<User[]>('getUsers')));
  }

  addUser(user: User): Observable<User> {
    return this.httpClient
      .post<User>('/api/users', user/*, httpOptions*/)
      .pipe(
        tap(a => this.log(`Added user item with id "${a.id}"`)),
        catchError(this.handleError<User>('addUser')));
  }

  updateUser(user: User): Observable<User> {
    return this.httpClient
      .put<User>('/api/users/' + user.id, user)
      .pipe(
        tap(a => this.log(`Updated user with id "${a.id}"`)),
        catchError(this.handleError<User>('updateUser')));
  }

  deleteUser(id: number): Observable<any> {
    return this.httpClient.delete<Message>('/api/users/' + id).pipe(
      tap(_ => this.log(`User Deleted!`)), catchError(this.handleError<Message>('deleteUser')));
  }



  private log(message: String) {
    // this.messageService.add('SubjectService: ' + message);
    alert('UserService: ' + message);
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log(error);
      this.log(`${operation} failed: ${error.message}`);
      return of (result as T);
    };
  }
}
